import React from 'react';
import './Home.css';
import { Panel, Cell, List, PanelHeader, Group, Div } from '@vkontakte/vkui';

class Home extends React.Component {
	constructor(props) {
		super(props);
	}

	render() {

		let {
			id, items
		} = this.props

		const alt = 'something'

		return (
			<Panel id={id}>
				<PanelHeader>Literature quotes</PanelHeader>
				<Group className="centered">
					<List>
						{
							 items.map((data, index) => (
									<Cell className="photoNumber" key={index}>
										<img src={data.photo_604} alt={alt}/><div><p>{index+1}</p></div>
								</Cell>
							))
						}
					</List>
				</Group>
			</Panel>
		);
	}
}
export default Home;
